.services {
  padding-bottom: 50px;
}

.services_wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.services_item {
  margin-right: 12.5px;
  margin-left: 12.5px;
  margin-top: 50px;
  background: linear-gradient(0deg, #3E7594, #3E7594), #C4C4C4;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  flex-grow: 0;
  flex-basis: 407px;
  transition: transform 0.6s;
  transform: scale(1);

  &:hover {
    transform: scale(1.1);
    transition: transform 0.6s;
  }
}

.services_img {
  width: 100%;
  height: auto;
}

.services_description {
  display: flex;
  flex-direction: row;
  align-items: center;
  min-height: 77px;
  height: 100%;
  padding-left: 65px;
}

.services_title {
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #FFFFFF;
  border-left: 4px solid #FBA500;
  padding-left: 24px;
  text-align: left;
  margin-top: 10px;
  margin-bottom: 10px;

  &::after {
    content: url("./../img/Arrow4.svg");
    margin-left: 24px;
    visibility: hidden;
  }
}

.services_item:hover .services_title::after {
  visibility: visible;
}
