.menu_burger {
  display: none;
  width: 50px;
  height: 50px;
  border: none;
  position: relative;
  background: transparent; }
  .menu_burger::before {
    content: '';
    display: block;
    width: 50px;
    height: 6px;
    background-color: #FFB800;
    position: absolute;
    right: 0;
    top: 15px;
    border-radius: 3px;
    transition: transform 0.3s;
    transform: rotate(0); }
  .menu_burger::after {
    content: '';
    display: block;
    width: 40px;
    height: 6px;
    background-color: #FFB800;
    position: absolute;
    right: 0;
    top: 25px;
    border-radius: 3px;
    transition: transform 0.3s;
    transform: rotate(0); }
  .menu_burger.open::before {
    transform: rotate(-135deg);
    top: 21px;
    right: 2px;
    transition: transform 0.3s; }
  .menu_burger.open::after {
    content: '';
    width: 50px;
    transform: rotate(135deg);
    top: 21px;
    right: 1px;
    transition: transform 0.3s; }
  .menu_burger:focus, .menu_burger:active {
    outline: none; }

