.header {
  position: relative;
  padding-bottom: 64px;
}

.header_background {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: linear-gradient(360deg, rgba(33, 54, 74, 0.76) 10.28%, rgba(0, 0, 0, 0) 62.39%), url("./../img/header_1x.png") no-repeat;
  background-size: auto 100%;
  z-index: -999;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 54.5%;
    backdrop-filter: blur(80px);
    background: rgba(0, 0, 0, 0.2);
  }
}

@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
  .header_background {
    background: linear-gradient(360deg, rgba(33, 54, 74, 0.76) 10.28%, rgba(0, 0, 0, 0) 62.39%), url("./../img/header_2x.png") no-repeat;
    background-size: auto 100%;
  }
}

.header_bottom {
  display: flex;
  flex-direction: row;
}

.header_slogans {
  margin-top: 304px;
  padding-left: 59px;
  flex-basis: 54.5%;
  box-sizing: border-box;
}

.header_advantages {
  display: flex;
  flex-direction: row;
  list-style: none;
  padding-left: 0;
}

.header_advantage {
  border-left: 4px solid #FBA500;
  padding-left: 24px;
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
  max-width: 417px;
  
  & span {
    color: #FFB800;
  }
}

.header_title {
  font-family: $Montserrat;
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
  line-height: 78px;
  color: #FFFFFF;
  margin-bottom: 0;
}

.header_subtitle {
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #FFFFFF;
  margin-top: 18px;
  margin-bottom: 39px;
}

.header_socials svg {
  margin-right: 42px;
  transform: scale(1);
  transition: transform 0.6s;

  &:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: transform 0.6s;
  }
}

@media(max-width: 1600px) {
  .header_background::before {
    left: 51.5%;
  }
}

// ************************************************************** меняется соотношение в хэдэре

@media(max-width: 1300px) {
  .header {
    padding-bottom: 0;
  }

  .header_bottom {
    flex-direction: column;
    align-items: center;
  }

  .header_background {
    background: linear-gradient(360deg, rgba(33, 54, 74, 0.76) 10.28%, rgba(0, 0, 0, 0) 62.39%), url("./../img/mob.png") no-repeat 100% auto;
    background-size: auto, auto 50%;

    &::before {
      display: none;
    }
  }

  .header_slogans {
    margin-top: 0;
    padding-top: 150px;
  }

  .header_title {
    font-size: 54px;
  }
}

@media(max-width: 1000px) {
  .header_title {
    font-size: 44px;
    line-height: 44px;
  }

  .header_advantage {
    font-size: 18px;
    line-height: 20px;
  }
}

@media(max-width: 600px) {
  .header_slogans {
    padding-left: 30px;
  }

  .header_advantages {
    flex-direction: column;
  }

  .header_advantage {
    margin-bottom: 30px;
  }
}

@media(max-width: 500px) {
  .menu_logo img {
    width: 100px;
  }

  .header_advantages {
    flex-wrap: wrap;
  }

  .header_title {
    font-size: 30px;
    line-height: 34px;
  }
}