.request {
  display: flex;
  flex-direction: column;
  padding-left: 123px;
  margin-top: 43px;
  box-sizing: border-box;
  flex-basis: 45.5%;
  position: relative;
}

.request_wrapper {
  display: flex;
  flex-direction: row;
}

.request_title {
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 48px;
  color: #FFFFFF;
  margin-bottom: 0;

  & span {
    color: #FBA500;
  }
}

.request_select {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  position: absolute;
  right: 112px;
  top: 43px;

  &::after {
    content: '';
    width: 0;
    height: 0;
    border: 5px solid transparent;
    border-top-color: #FFB800;
    border-bottom: 0;
    margin-left: 4px;
  }

  &:hover {
    cursor: pointer;
  }
}

.request_regions {
  background: transparent;
  border: none;
  font-family: Circe;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
  width: 74px;

  &:hover {
    cursor: pointer;
    color: $orange;
  }

  &:focus,
  &:active {
    outline: none;
  }
}

.request_regions-list {
  display: none;
  padding-left: 0;
  list-style: none;
  font-family: Circe;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
  position: absolute;
  top: 14px;
  left: 5px;
}

.request_regions-item {
  margin-top: 15px;
  margin-bottom: 15px;

  &:hover {
    cursor: pointer;
    color: $orange;
  }
}

.request_select.open .request_regions-list {
  display: block;
}

.request_subtitle {
  font-family: $Circe;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  color: #FFFFFF;
  margin-top: 19px;
  border-left: 3px solid #FFB800;
  padding-left: 22px;
  margin-bottom: 14px;
}

.request_field-name {
  font-family: $Circe;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  margin-top: 22px;
  margin-bottom: 8px;
}

.request_group {
  display: flex;
  flex-direction: column;
}

.request_field-name {
  font-family: $Circe;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #FFFFFF;
  padding-left: 13px;
}

.request_field-wrapper {
  border-radius: 3px;
  position: relative;
  max-width: 462px;
}

.request_field-background {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 3px;
  background: rgba(255, 255, 255, 0.4);
  box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.16);
  backdrop-filter: blur(16px);
  z-index: -66;
}

.request_field {
  background: transparent;
  box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.16);
  border-radius: 3px;
  border: none;
  font-family: Circe;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  padding: 9px 10px;
  width: 100%;
  box-sizing: border-box;

  &:focus,
  &:active {
    outline: none;
  }

  &::placeholder {
    font-family: $Circe;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #C7C7C7;
  }

  &--message {
    resize: none;
    min-height: 88px;
  }
}

.request_submit {
  background: #FBA500;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.16), 2px 4px 4px rgba(0, 0, 0, 0.08);
  border-radius: 3px;
  border: none;
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;
  color: #FFFFFF;
  margin-top: 39px;
  padding: 12px 45px;

  &:focus,
  &:active {
    outline: none;
  }

  &:hover {
    background: #EFAC2B;
    cursor: pointer;
  }

  &:active {
    background: #EEB037;
  }
}

.request_email {
  font-family: $Circe;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #B1B1B1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-decoration: none;
  margin-top: 47px;
  margin-left: 38px;

  & span {
    font-family: $Circe;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    border-left: 4px solid #FBA500;
    margin-top: 6px;
    padding-left: 14px;
  }

  &:hover {
    cursor: pointer;
    color: white;
  }

  &:hover span {
    color: #B1B1B1;
  }
}

@media(max-width: 1800px) {
  .request_select {
    right: 30px;
  }
}

@media(max-width: 1600px) {
  .request {
    padding-left: 80px;
  }
}

@media(max-width: 1500px) {
  .request {
    padding-left: 50px;
  }
}

@media(max-width: 1400px) {
  .request {
    padding-left: 0;
  }

  .request_subtitle {
    margin-bottom: 45px;
  }

  .request_select {
    position: relative;
    top: 0;
    left: 0;
  }

  .request_select.open .request_regions-list {
    background-color: $orange;
    padding: 30px;
    border-radius: 3px;
    box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.16);
    z-index: 999;
  }
}

@media(max-width: 1300px) {
  .request_wrapper {
    flex-direction: column;
  }

  .request {
    margin-top: 150px;
    padding-bottom: 30px;
  }
}

@media(max-width: 500px) {
  .request {
    padding-left: 30px;
    padding-right: 30px;
  }

  .request_title {
    font-size: 30px;
    line-height: 30px;
  }
}