.menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 30px;
  padding-left: 30px;
  padding-top: 60px;
}

.menu_burger {
  display: none;
  width: 50px;
  height: 50px;
  border: none;
  position: relative;
  background: transparent;

  &::before {
    content: '';
    display: block;
    width: 50px;
    height: 6px;
    background-color: $orange;
    position: absolute;
    right: 0;
    top: 15px;
    border-radius: 3px;
    transition: transform 0.3s;
    transform: rotate(0);
  }

  &::after {
    content: '';
    display: block;
    width: 40px;
    height: 6px;
    background-color: $orange;
    position: absolute;
    right: 0;
    top: 25px;
    border-radius: 3px;
    transition: transform 0.3s;
    transform: rotate(0);
  }

  &.open {
    &::before {
      transform: rotate(-135deg);
      top: 21px;
      right: 2px;
      transition: transform 0.3s;
    }
  
    &::after {
      content: '';
      width: 50px;
      transform: rotate(135deg);
      top: 21px;
      right: 1px;
      transition: transform 0.3s;
    }
  }

  &:focus,
  &:active {
    outline: none;
  }
}

.menu_list,
.menu_submenu-list {
  padding-left: 0;
  list-style: none;
}

.menu_list {
  display: flex;
  flex-direction: row;
  align-self: stretch;
  margin-top: 0;
  margin-bottom: 0;
}

.menu_item {
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;
  color: #FFFFFF;
  text-decoration: none;
  margin-right: 36px;

  &--phone {
    display: flex;
    flex-direction: column;
    font-family: $Circe;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-align: right;
    color: #B1B1B1;
    margin-right: 0;
    margin-top: -15px;
    margin-left: 41px;

    & span {
      font-family: $Circe;
      font-style: normal;
      font-weight: 800;
      font-size: 32px;
      line-height: 32px;
      color: #FFFFFF;
      border-left: 4.4px solid #FBA500;
      margin-top: 8px;
      padding-left: 19px;
    }

    &:hover {
      color: white;
    }

    &:hover span {
      color: #B1B1B1;
    }
  }

  &--with-submenu {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;

    &::after {
      content: '';
      width: 0;
      height: 0;
      border: 5px solid transparent;
      border-top-color: #FFB800;
      border-bottom: 0;
      margin-left: 7px;
    }
  }
}

@media(min-width: 900px) {
  .menu_item {
    &:hover {
      color: $orange;
    }

    &:hover ~ .menu_submenu-list{
      display: block;
    }
  }
}

.menu_submenu-list {
  display: none;
  position: absolute;
  background: $orange;
  z-index: 999;
  padding: 25px;
  border-radius: 3px;
  box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.16);

  &:hover {
    display: block;
  }
}

.menu_submenu-item {
  font-family: $Circe;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;
  color: #FFFFFF;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    color: black;
  }
}

@media(max-width: 1500px) {
  .menu_list {
    position: relative;
  }

  .menu_item {
    margin-right: 30px;

    &--phone {
      margin-left: 20px;
      position: absolute;
      top: 72px;
      right: 0px;
    }
  }
}

@media(max-width: 1300px) {
  .menu_item--phone {
    color: white;
    position: static;
  }
}

@media(max-width: 900px) {
  .menu {
    position: relative;
    padding-left: 0;
  }

  .menu_logo {
    padding-left: 30px;
  }

  .menu_list {
    position: absolute;
    flex-direction: column;
    top: 128px;
    width: 100%;
    box-sizing: border-box;
    background: $orange;
    overflow: hidden;
    height: 0;

    & li {
      transform: translateY(-500px);
      transition: transform 0.3s;
    }
  }

  .menu_burger {
    display: block;
  }

  .menu_burger.open ~ .menu_list {
    height: auto;
    padding-bottom: 30px;
    padding-top: 30px;

    & li {
      transform: translateY(0);
      transition: transform 0.3s;
    }
  }

  .menu_item {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 18px;
    font-family: $Circe;
    font-style: normal;
    font-weight: 700;
    margin-right: 0;
    padding-left: 30px;
    padding-right: 30px;

    &--phone {
      position: static;
      color: white;
      text-align: left;
      margin-left: 0;

      & span {
        border: none;
        padding-left: 0;
      }
    }

    &--with-submenu::after {
      border-top-color: white;
      transform: rotate(0);
    }

    &--with-submenu.open ~ .menu_submenu-list {
      display: block;
    }
  }

  .menu_submenu-list {
    position: static;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: none;
    border-top: 1px solid rgba(0, 0, 0, 0.16);
    border-bottom: 1px solid rgba(0, 0, 0, 0.16);
    box-shadow: none;
    margin-bottom: 10px;
  }

  .menu_submenu-item {
    display: block;
    font-size: 18px;
    line-height: 18px;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}