.process {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-right: 63px;
  padding-left: 63px;
  padding-top: 119px;
  padding-bottom: 65px;
  box-sizing: border-box;
}

.process_step {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-grow: 1;
  flex-basis: 200px;

  & svg {
    flex-shrink: 0;
  }
}

.process_arrow {
  flex-shrink: 0;
  margin-right: 57px;
  margin-left: 57px;
}

.process_description {
  font-family: $Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #255D7D;
  margin-left: 23px;

  & a {
    color: $orange;

    &:hover {
      color: rgb(6, 34, 49);
    }
  }
}

@media(max-width: 1150px) {
  .process {
    padding-right: 30px;
    padding-left: 30px;
    padding-top: 50px;
    padding-bottom: 50px;
    flex-direction: column;
  }

  .process_arrow {
    transform: rotate(90deg);
  }

  .process_description {

    & br {
      display: none;
    }
  }
}